.highlow {
    background-color: black;
    display: flex;
    width: 720px;
    background: black;
    margin: 0 auto;
}

.highlowWrapper {
    background: black;
}

.highlow * {
    margin: 0;
    padding: 0;
    border: none;
    justify-content: space-between;
    background: black;
    color: lightgrey;
    border: none;
}

.squad {
    height: 40px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 5px;
}

.bets {
    width: 300px;
}

.highlow input.betValue {
    margin-left: 15px;
    padding: 14px 0 14px 5px;
    box-shadow: 1px 1px 7px 1px lightpink;
    width: 265px;
    /* height: 40px; */
}


.number {
    box-shadow: 1px 1px 5px 1px lightpink;
}



.highlow .squad {
    /* border: 5px blue solid; */
    box-shadow: 1px 1px 7px 1px lightpink;
    margin: 15px;

}


.highlow .after {
    width: 255px;
}

@media screen and (max-width: 720px) {
    .highlow {
        width: 100%;
        flex-direction: column;
    }
}